import React from "react";
import Carousel from "react-bootstrap/Carousel";
import "./Hero.scss";

const images = [
  "https://i.imgur.com/E8J3lam.jpg", //
  "https://i.imgur.com/M01YyQu.jpg", //
  "https://i.imgur.com/CLhlP9L.png", //
  "https://i.imgur.com/fsXqb1t.png", //
  "https://i.imgur.com/ZZfMD96.png", //
  "https://i.imgur.com/YEbX7CF.png", //
];

const imagesMob = [
  "https://i.imgur.com/9fVvYug.png", //
  "https://i.imgur.com/mJiqDLB.png", //
  "https://i.imgur.com/16vtDEz.png", //
  "https://i.imgur.com/dMn2zAC.png", //
  "https://i.imgur.com/V20JtYN.png", //
  "https://i.imgur.com/TzEaVc2.png", //
];

const Hero = () => {
  let details = navigator.userAgent;
  let regexp = /android|iphone|kindle|ipad/i;
  let isMobileDevice = regexp.test(details);

  return (
    <div className="sec-hero">
      <Carousel className="car" id="inicio" interval={4500}>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[0] : images[0]}
            alt="First slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>Bienvenidos a Torres Magistrados</h3>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[1] : images[1]}
            alt="Second slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>2 torres, 70 departamentos, 7000m2</h3>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[2] : images[2]}
            alt="Tercer slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>Diseño versátil</h3>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[3] : images[3]}
            alt="Cuarto slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>Ubicación privilegiada</h3>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[4] : images[4]}
            alt="Quinto slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>Terminaciones de primer nivel</h3>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item className="car-item">
          <img
            className="car-img-hero"
            loading="lazy"
            src={isMobileDevice ? imagesMob[5] : images[5]}
            alt="Sexto slide"
          />
          <Carousel.Caption className="car-item-caption">
            <h3>Financiación innovadora</h3>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>
    </div>
  );
};

export default Hero;
